class ProjectOverlayClass {
  constructor() {
    this.slug = null;
    this.fetchedProjects = {};
  }

  fetchProject(slug) {
    this.fetchedProjects[slug] = {};
  }
  
  open(slug) {
    if (this.slug !== slug && !this.fetchedProjects[slug]) {
      console.log('fetch it');
    }
    this.slug = slug;
  };

  close() {
    console.log('close');
  };
};

export default ProjectOverlayClass;



